import coursesStyles from "./Courses.module.css";
import { IconLoader } from "@tabler/icons-react";
import {
	baseURL,
	resourceName,
	getTrainingProgrammesEndpt,
	awareIMContextPath,
	newTrainingEventEndpt,
} from "../../utils/endpoints";
import {
	courses_noRecordsFoundText,
	courses_modalSuccessHeadingText,
	courses_modalFailureHeadingText,
	courses_modalSucessSubheadingText,
	courses_modalfailureSubheadingText,
	contactEmailAddress,
} from "../../utils/infoStrings";

import Modal from "react-modal";
import Cookies from "js-cookie";

import { useUpdateHeader } from "../../utils/functionList";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import CourseCard from "./CourseCard";
import SearchBar from "../partials/searchbar/SearchBar";

// Modal styles have to be an object defined in this component. Do not move this to a stylesheet
const modalStyles = {
	overlay: {
		backgroundColor: "rgba(12, 9, 30, 0.7)",
		backdropFilter: "blur(1px)",
		zIndex: 9999,
	},
	content: {
		position: "absolute",
		top: "50%",
		left: "50%",
		transform: "translate(-50%, -50%)",
		width: "271px",
		height: "265px",
		maxWidth: "90%",
		maxHeight: "90%",
		overflow: "auto",
		background: "#ffffff",
		borderRadius: "4px",
		padding: "20px",
		boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.2)",
	},
};

Modal.setAppElement("#root");

const Courses = () => {
	useUpdateHeader(
		{ isBackButton: true, backLink: "/alldata", confirmOnExit: false },
		"Courses",
		false,
		"Courses"
	);

	const navigate = useNavigate();

	const [courseList, setCourseList] = useState([]);
	const [selectedCourse, setSelectedCourse] = useState();
	const [searchTerm, setSearchTerm] = useState("");
	const [submitResponse, setSubmitResponse] = useState();
	const [showModal, setShowModal] = useState(false);
	const [isSubmitLoading, setIsSubmitLoading] = useState(false);
	const [areCoursesLoading, setAreCoursesLoading] = useState(true);

	const loggedInUser = Cookies.get("user")
		? JSON.parse(Cookies.get("user"))
		: "";

	const traineeList = localStorage.getItem("employeeList")
		? JSON.parse(localStorage.getItem("employeeList"))
		: [];

	const getCourses = async () => {
		const res = await fetch(
			`${baseURL}${resourceName}${getTrainingProgrammesEndpt}`,
			{
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify({
					api_key: loggedInUser.token,
					member_id: loggedInUser.id,
				}),
			}
		);
		const resData = await res.json();
		// console.log(resData);

		// If token validation failed or token is expired
		if (resData.status === 3) {
			Cookies.remove("user");

			sessionStorage.setItem(
				"messageToFlash",
				JSON.stringify({
					status: 0,
					message: resData.message + `. Please login again`,
					showFlashMessage: true,
				})
			);
			window.location.reload(true);
		}

		setCourseList(resData.courses);
		setAreCoursesLoading(false);
	};

	useEffect(() => {
		getCourses();
	}, []);

	const handleModalBtnClick = () => {
		navigate("/", { replace: true });
	};

	const handleSubmit = async () => {
		setIsSubmitLoading(true);
		const timeNow = Date.now();
		let resData;

		if (navigator.onLine) {
			const submitBodyParams = {
				api_key: loggedInUser.token,
				member_id: loggedInUser.id,
				training_event: {
					scheduled_on: timeNow,
					course_id: selectedCourse.id,
					participants: traineeList.map((trainee) => ({
						participant_id: trainee.id,
					})),
				},
			};

			const res = await fetch(
				`${baseURL}${awareIMContextPath}${newTrainingEventEndpt}`,
				{
					method: "POST",
					headers: {
						"Content-Type": "application/json",
					},
					body: JSON.stringify(submitBodyParams),
				}
			);
			resData = await res.json();
			// console.log(resData);
		} else {
			resData = { status: 0 };
		}

		addTrainingEvent(timeNow, resData);
		setIsSubmitLoading(false);
	};

	const addTrainingEvent = (timeNow, resData) => {
		const trainingEvent = {
			event: {
				scheduled_on: timeNow,
				trainer: {
					member_id: loggedInUser.id,
					name: loggedInUser.name,
				},
				trainees: traineeList,
				course: selectedCourse,
			},
		};
		trainingEvent.status = resData.status;
		// console.log(trainingEvent);

		const allTrainingEvents = localStorage.getItem("allTrainingEvents")
			? JSON.parse(localStorage.getItem("allTrainingEvents"))
			: [];
		allTrainingEvents.push(trainingEvent);
		// console.log(trainingEvent);
		localStorage.setItem(
			"allTrainingEvents",
			JSON.stringify(allTrainingEvents)
		);

		localStorage.removeItem("employeeList");
		setSubmitResponse(resData);
		setShowModal(true);
	};

	useEffect(() => {
		if (!showModal && (!traineeList.length || !loggedInUser)) {
			navigate("/");
		}
	}, [showModal]);

	return (
		<>
			<div className={coursesStyles.container}>
				<div className={coursesStyles.searchContainer}>
					<SearchBar
						searchTerm={searchTerm}
						setSearchTerm={setSearchTerm}
					/>
				</div>

				<div className={coursesStyles.courseList}>
					{!areCoursesLoading ? (
						<ul>
							{courseList.filter((course) =>
								course.name
									.toLowerCase()
									.includes(searchTerm.toLowerCase())
							).length ? (
								courseList
									.filter((course) =>
										course.name
											.toLowerCase()
											.includes(searchTerm.toLowerCase())
									)
									.map((course, index) => {
										return (
											<li key={course.id}>
												<CourseCard
													course={course}
													index={index}
													selectedCourse={
														selectedCourse
													}
													setSelectedCourse={
														setSelectedCourse
													}
												/>
											</li>
										);
									})
							) : (
								<div className={coursesStyles.noCoursesText}>
									{courses_noRecordsFoundText}
								</div>
							)}
						</ul>
					) : (
						<IconLoader className="loaderSvg" />
					)}
				</div>
			</div>

			<div className={coursesStyles.submitBtnContainer}>
				<div className={coursesStyles.submitDiv}>
					<button
						className={coursesStyles.submitBtn}
						onClick={handleSubmit}
						disabled={!selectedCourse ? true : false}>
						{!isSubmitLoading ? (
							"Submit"
						) : (
							<IconLoader className="loaderSvg" />
						)}
					</button>
				</div>
			</div>

			{/* Modal */}
			<Modal
				isOpen={showModal}
				onRequestClose={() => setShowModal(false)}
				style={modalStyles}
				contentLabel="Response Modal"
				parentSelector={() => document.querySelector(`#root`)}>
				<div className={coursesStyles.modalContainer}>
					<div className={coursesStyles.modalImg}>
						<img
							src={
								submitResponse?.status
									? "/images/success_img.png"
									: "/images/failed_img.png"
							}
							alt="Submit Status"
						/>
					</div>
					<div className={coursesStyles.modalContent}>
						<span className={coursesStyles.modalTitle}>
							{submitResponse?.status
								? courses_modalSuccessHeadingText
								: courses_modalFailureHeadingText}
						</span>
						<div className={coursesStyles.modalText}>
							{submitResponse?.status ? (
								<p>{courses_modalSucessSubheadingText}</p>
							) : (
								<p>
									{courses_modalfailureSubheadingText}{" "}
									<a href={`mailto:${contactEmailAddress}`}>
										{contactEmailAddress}
									</a>
								</p>
							)}
						</div>
					</div>
					<button
						className={`${coursesStyles.modalBtn} ${
							submitResponse?.status
								? coursesStyles.modalSuccess
								: coursesStyles.modalFailed
						}`}
						onClick={handleModalBtnClick}>
						{submitResponse?.status ? "Ok" : "Try Again"}
					</button>
				</div>
			</Modal>
		</>
	);
};

export default Courses;
