import scannerStyles from "./Scanner.module.css";
import {
	scanner_headingText,
	scanner_defaultText,
	scanner_errorText,
	scanner_checkDevicePermissionsText,
} from "../../utils/infoStrings";

import jsQR from "jsqr";

import { useUpdateHeader } from "../../utils/functionList";
import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";

const Scanner = () => {
	useUpdateHeader(
		{ isBackButton: true, backLink: "/", confirmOnExit: true },
		"Scanner",
		false,
		"Scanner"
	);

	const videoRef = useRef(null);
	const canvasRef = useRef(null);
	const [heroText, setHeroText] = useState(scanner_defaultText);
	const navigate = useNavigate();

	useEffect(() => {
		const video = videoRef.current;
		const canvas = canvasRef.current;
		const ctx = canvas.getContext("2d");

		const constraints = { video: { facingMode: "environment" } };

		navigator.mediaDevices
			.getUserMedia(constraints)
			.then((stream) => {
				video.srcObject = stream;
				video.play().catch((error) => {
					// console.log(
					// 	"An error occurred while trying to play the video:",
					// 	error
					// );
					setHeroText(scanner_errorText);
				});
				requestAnimationFrame(scanQRCode);
			})
			.catch((error) => {
				// console.error("Error accessing camera:", error);
				setHeroText(scanner_checkDevicePermissionsText);
			});

		const scanQRCode = () => {
			if (video.readyState === video.HAVE_ENOUGH_DATA) {
				const videoWidth = video.videoWidth;
				const videoHeight = video.videoHeight;
				canvas.width = videoWidth;
				canvas.height = videoHeight;
				ctx.drawImage(video, 0, 0, videoWidth, videoHeight);
				const imageData = ctx.getImageData(
					0,
					0,
					videoWidth,
					videoHeight
				);
				const code = jsQR(imageData.data, videoWidth, videoHeight);
				if (code && code.data !== "") {
					// console.log("QR Code detected:", code.data);
					handleQRCodeDetected(code.data);
				}
			}
			requestAnimationFrame(scanQRCode);
		};

		return () => {
			if (video.srcObject) {
				video.srcObject.getTracks().forEach((track) => track.stop());
			}
		};
		// eslint-disable-next-line
	}, [navigate]);

	function handleQRCodeDetected(data) {
		navigate("/scanneddata", {
			state: {
				scannedData: data,
			},
		});
	}

	return (
		<>
			<div className={scannerStyles.container}>
				<div className={scannerStyles.infoBox}>
					<span className={scannerStyles.infoTitle}>
						{scanner_headingText}
					</span>
					<span className={scannerStyles.infoDetails}>
						{heroText}
					</span>
				</div>
				<div className={scannerStyles.scanWindow}>
					<div className={scannerStyles.videoContainer}>
						<video ref={videoRef} playsInline autoPlay />
						<canvas ref={canvasRef} style={{ display: "none" }} />
					</div>
				</div>
			</div>
		</>
	);
};

export default Scanner;
