import employeeDetailsStyles from "./EmployeeDetails.module.css";
import {
	IconCircleCheckFilled,
	IconAlertCircleFilled,
} from "@tabler/icons-react";
import {
	employeeDetails_successText,
	employeeDetails_alreadyScannedText,
} from "../../utils/infoStrings";

import Cookies from "js-cookie";

import { useUpdateHeader } from "../../utils/functionList";
import { useLocation, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import {
	baseURL,
	getEmployeeDetailsEndPt,
	resourceName,
} from "../../utils/endpoints";

const EmployeeDetails = () => {
	useUpdateHeader(
		{ isBackButton: false, backLink: "" },
		"Employee Details",
		false,
		"EmployeeDetails"
	);

	const navigate = useNavigate();

	const [employeeDetails, setEmployeeDetails] = useState();
	const [noDataError, setNoDataError] = useState();
	const [isDuplicatePresent, setIsDuplicatePresent] = useState(false);

	const location = useLocation();
	const { scannedData } = location.state;
	let storedData = localStorage.getItem("employeeList")
		? JSON.parse(localStorage.getItem("employeeList"))
		: [];

	storedData = storedData.includes(null) ? [] : storedData;

	async function getEmployeeDetails() {
		if (!scannedData.includes("workxid")) {
			setNoDataError("Invalid QR scan");
			return;
		}

		const loggedInUser = Cookies.get("user")
			? JSON.parse(Cookies.get("user"))
			: "";

		const res = await fetch(
			`${baseURL}${resourceName}${getEmployeeDetailsEndPt}`,
			{
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify({
					api_key: loggedInUser.token,
					member_id: loggedInUser.id,
					qr_code: scannedData,
				}),
			}
		);
		const resData = await res.json();
		// console.log(resData);

		if (
			resData.status === 0 ||
			resData.status === 101 ||
			resData.status === 102
		) {
			setNoDataError(resData.message);
		} else if (resData.status === 3) {
			Cookies.remove("user");

			sessionStorage.setItem(
				"messageToFlash",
				JSON.stringify({
					status: 0,
					message: resData.message + `. Please login again`,
					showFlashMessage: true,
				})
			);

			window.location.reload(true);
		} else if (!resData.member) {
			setNoDataError("Something went wrong. Try scanning the ID again.");
		} else {
			const relevantEmployeeDetails = {
				id: resData.member.id,
				fullName: resData.member.name,
				photo: baseURL + "/" + resData.member.photoURL,
				cardFrontImage: baseURL + "/" + resData.member.CardFrontImage,
				trade: resData.member.trade,
				employerName: resData.member.employerNameText,
			};
			// console.log(relevantEmployeeDetails);
			setEmployeeDetails(relevantEmployeeDetails);

			// Check if the data is already present in the array
			storedData.filter((employee) => employee.id === resData.member.id)
				.length > 0 && setIsDuplicatePresent(true);
		}
	}

	useEffect(() => {
		getEmployeeDetails();
		// eslint-disable-next-line
	}, []);

	function addEmployeeToList() {
		if (!isDuplicatePresent && !noDataError) {
			storedData.push(employeeDetails);
			localStorage.setItem("employeeList", JSON.stringify(storedData));
			// console.log(
			// 	"Employee details stored in Employee List local storage:",
			// 	storedData
			// );
		}
	}

	return (
		<>
			<div className={employeeDetailsStyles.wrapper}>
				<div className={employeeDetailsStyles.contentContainer}>
					<div className={employeeDetailsStyles.content}>
						<div className={employeeDetailsStyles.data}>
							{noDataError ? (
								<div
									className={
										employeeDetailsStyles.noDataError
									}>
									<IconAlertCircleFilled
										size={17}
										className={
											employeeDetailsStyles.alertSvg
										}
									/>
									&nbsp;{noDataError}
								</div>
							) : (
								employeeDetails && (
									<div>
										<div
											className={
												employeeDetailsStyles.scanMsg
											}>
											{isDuplicatePresent ? (
												<span
													className={
														employeeDetailsStyles.alert
													}>
													<IconAlertCircleFilled
														size={17}
														className={
															employeeDetailsStyles.alertSvg
														}
													/>
													&nbsp;
													{
														employeeDetails_alreadyScannedText
													}
												</span>
											) : (
												<span
													className={
														employeeDetailsStyles.success
													}>
													<IconCircleCheckFilled
														size={17}
														className={
															employeeDetailsStyles.successSvg
														}
													/>
													&nbsp;
													{
														employeeDetails_successText
													}
												</span>
											)}
										</div>
										<div
											className={
												employeeDetailsStyles.employeeCardImg
											}>
											{employeeDetails ? (
												// eslint-disable-next-line
												<img
													src={
														employeeDetails?.cardFrontImage
													}
													alt="Employee Card Front Image"
												/>
											) : (
												<img
													src="/images/placeholder_pfp.jpg"
													alt="Employee Card loading..."
												/>
											)}
										</div>
									</div>
								)
							)}
						</div>
					</div>
				</div>

				<div className={employeeDetailsStyles.postScanContainer}>
					<div className={employeeDetailsStyles.postScan}>
						<div
							onClick={() => {
								addEmployeeToList();
								navigate("/scan");
							}}
							className={`${employeeDetailsStyles.postScanBtn} ${
								employeeDetailsStyles.scanNextBtn
							} ${
								!noDataError && !employeeDetails
									? employeeDetailsStyles.postScanDisabled
									: ""
							}`}>
							{noDataError ? "Scan Again" : "Scan Next"}
						</div>
						{!noDataError && (
							<div
								onClick={() => {
									addEmployeeToList();
									navigate("/alldata");
								}}
								className={`${
									employeeDetailsStyles.postScanBtn
								} ${employeeDetailsStyles.doneBtn}  ${
									!employeeDetails
										? employeeDetailsStyles.postScanDisabled
										: ""
								}`}>
								Done
							</div>
						)}
					</div>
				</div>
			</div>
		</>
	);
};

export default EmployeeDetails;
