import menuStyles from "./Menu.module.css";

import Cookies from "js-cookie";

import { useUpdateHeader } from "../../utils/functionList";

import FloatingMenu from "./floatingMenu/FloatingMenu";
import MenuCard from "./MenuCard";
import FlashMessage from "../partials/flashMessage/FlashMessage";

const Menu = () => {
	useUpdateHeader(
		{ isBackButton: false, backLink: "", confirmOnExit: false },
		"",
		false,
		"Menu"
	);

	const user = Cookies.get("user") ? JSON.parse(Cookies.get("user")) : "";
	// console.log(user);

	return (
		<>
			<div className={menuStyles.menuWrapper}>
				<div className={menuStyles.orgContainer}>
					<div className={menuStyles.trainerDiv}>
						<div className={menuStyles.trainerPhoto}>
							<img src={user.photo} alt={user.name} />
						</div>
						<div className={menuStyles.trainerName}>
							<span>
								{user.name.split(" ")[1]?.toUpperCase()}
							</span>
							<span>{user.name.split(" ")[0]}</span>
						</div>
					</div>

					<div className={menuStyles.orgLogo}>
						<img src={user.companyLogo} alt="Cobalt Safety logo" />
					</div>
				</div>

				<MenuCard />

				{/* Login flash message */}
				<FlashMessage />

				<FloatingMenu />
			</div>
		</>
	);
};

export default Menu;
