import eventCardStyles from "./EventCard.module.css";
import { IconClockFilled } from "@tabler/icons-react";

const EventCard = ({ event }) => {
	function eventDateAndTime(event) {
		// Derive date from milliseconds
		const completeEventTime = new Date(event.event.scheduled_on);

		const dateOfEvent =
			completeEventTime.getDate() < 10
				? "0" + completeEventTime.getDate()
				: completeEventTime.getDate();

		let monthofEvent = completeEventTime.getMonth() + 1;
		monthofEvent = monthofEvent < 10 ? "0" + monthofEvent : monthofEvent;

		const yearOfEvent = completeEventTime.getFullYear();

		const fullDate = dateOfEvent + "." + monthofEvent + "." + yearOfEvent;

		// Derive time from milliseconds
		let hour24ofEvent = completeEventTime.getHours();
		let minutesOfEvent = completeEventTime.getMinutes();

		const AMorPM = hour24ofEvent - 12 < 0 ? "AM" : "PM";
		let hour12ofEvent;
		if (hour24ofEvent !== 12) {
			hour12ofEvent = hour24ofEvent % 12;
		} else {
			hour12ofEvent = hour24ofEvent;
		}

		hour12ofEvent =
			hour12ofEvent < 10 ? "0" + hour12ofEvent : hour12ofEvent;
		minutesOfEvent =
			minutesOfEvent < 10 ? "0" + minutesOfEvent : minutesOfEvent;

		const fullTime = hour12ofEvent + ":" + minutesOfEvent + " " + AMorPM;

		return fullDate + ", " + fullTime;
	}

	return (
		<>
			<div className={eventCardStyles.trainingEventDiv}>
				<div className={eventCardStyles.eventHeader}>
					<span className={eventCardStyles.courseId}>
						{event.event.course.courseID}
					</span>
					<span className={eventCardStyles.eventDateAndTime}>
						<IconClockFilled
							size={10}
							className={eventCardStyles.clockSvg}
						/>
						{eventDateAndTime(event)}
					</span>
				</div>

				<div className={eventCardStyles.eventBody}>
					<div className={eventCardStyles.courseName}>
						{event.event.course.name.length > 83
							? event.event.course.name.substring(0, 83) + "..."
							: event.event.course.name}
					</div>
					<div className={eventCardStyles.bottomDiv}>
						<div className={eventCardStyles.trainerAndTrainees}>
							<div className={eventCardStyles.trainerName}>
								<span
									className={
										eventCardStyles.trainerAndTraineesSvg
									}>
									<svg
										width="9"
										height="10"
										viewBox="0 0 9 10"
										fill="none"
										xmlns="http://www.w3.org/2000/svg">
										<path
											fillRule="evenodd"
											clipRule="evenodd"
											d="M7.3997 3.17582C7.3997 4.4977 6.2573 5.55703 4.83176 5.55703C3.40674 5.55703 2.26383 4.4977 2.26383 3.17582C2.26383 1.85394 3.40674 0.794617 4.83176 0.794617C6.2573 0.794617 7.3997 1.85394 7.3997 3.17582ZM0.950928 8.2533C0.950928 7.02964 2.73866 6.72348 4.83176 6.72348C6.93623 6.72348 8.7126 7.04018 8.7126 8.2648C8.7126 9.48846 6.92486 9.79462 4.83176 9.79462C2.7273 9.79462 0.950928 9.47792 0.950928 8.2533Z"
											fill="#140E34"
											fillOpacity="0.5"
										/>
									</svg>
								</span>
								{event.event.trainer.name.toUpperCase()}
							</div>
							<div className={eventCardStyles.numOfTrainees}>
								<span
									className={
										eventCardStyles.trainerAndTraineesSvg
									}>
									<svg
										width="12"
										height="10"
										viewBox="0 0 12 10"
										fill="none"
										xmlns="http://www.w3.org/2000/svg">
										<path
											fillRule="evenodd"
											clipRule="evenodd"
											d="M7.43217 2.88096C7.43217 4.20284 6.28977 5.26217 4.86423 5.26217C3.43921 5.26217 2.2963 4.20284 2.2963 2.88096C2.2963 1.55908 3.43921 0.499756 4.86423 0.499756C6.28977 0.499756 7.43217 1.55908 7.43217 2.88096ZM0.983398 7.95844C0.983398 6.73478 2.77114 6.42862 4.86423 6.42862C6.9687 6.42862 8.74507 6.74532 8.74507 7.96994C8.74507 9.1936 6.95733 9.49976 4.86423 9.49976C2.75977 9.49976 0.983398 9.18306 0.983398 7.95844ZM8.46771 2.92413C8.46771 3.59729 8.24967 4.22541 7.86732 4.74717C7.82754 4.80083 7.86267 4.87317 7.93191 4.88467C8.02801 4.89952 8.1267 4.90863 8.22745 4.91054C9.22983 4.93498 10.1294 4.33656 10.3779 3.43535C10.7463 2.09813 9.66539 0.89747 8.28842 0.89747C8.1391 0.89747 7.99598 0.911843 7.85647 0.938195C7.83735 0.942028 7.81669 0.950652 7.80635 0.965984C7.79292 0.985627 7.80274 1.01102 7.81617 1.02779C8.23004 1.56584 8.46771 2.22079 8.46771 2.92413ZM10.1278 5.8509C10.8016 5.97308 11.2444 6.22174 11.4278 6.58443C11.5833 6.88148 11.5833 7.22645 11.4278 7.5235C11.1473 8.08502 10.242 8.26565 9.89015 8.31212C9.8173 8.32171 9.75891 8.26421 9.76666 8.19618C9.94647 6.64001 8.5168 5.90217 8.14685 5.73256C8.13135 5.72442 8.12773 5.71292 8.12928 5.70525C8.13032 5.70046 8.13703 5.6928 8.14892 5.69136C8.94927 5.67699 9.81007 5.77904 10.1278 5.8509Z"
											fill="#140E34"
											fillOpacity="0.5"
										/>
									</svg>
								</span>
								{event.event.trainees.length < 10
									? "0" + event.event.trainees.length
									: event.event.trainees.length}
							</div>
						</div>
						<div
							className={`${eventCardStyles.eventStatus} ${
								event.status
									? eventCardStyles.complete
									: eventCardStyles.pending
							}`}>
							{event.status === 1 ? "completed" : "Pending"}
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default EventCard;
