import menuCardStyles from "./MenuCard.module.css";
import { IconScan } from "@tabler/icons-react";
import { IconHistory } from "@tabler/icons-react";
import { IconLogout2 } from "@tabler/icons-react";
// import { IconClipboardList } from "@tabler/icons-react";
import MenuButton from "./MenuButton";

const MenuCard = () => {
	return (
		<>
			<div className={menuCardStyles.menuContainer}>
				<div className={menuCardStyles.menuCard}>
					<MenuButton
						btnLink={"/scan"}
						btnText={"Scan"}
						btnIcon={<IconScan size={20} />}
					/>

					{/* <MenuButton
						btnLink={"/alldata"}
						btnText={"ViewScanned"}
						btnIcon={<IconClipboardList size={20} />}
					/> */}

					<MenuButton
						btnLink={"/history"}
						btnText={"Session History"}
						btnIcon={<IconHistory size={20} />}
					/>

					<MenuButton
						btnText={"Sign Out"}
						btnLink={null}
						btnIcon={<IconLogout2 size={20} />}
					/>
				</div>
			</div>
		</>
	);
};

export default MenuCard;
