import { IconTrashFilled } from "@tabler/icons-react";
import employeeInfoCardStyles from "./EmployeeInfoCard.module.css";

const EmployeeInfoCard = ({ user, employeeList, setEmployeeList }) => {
	function handleDelete(id) {
		const arr = [...employeeList];
		const updatedEmployeeList = arr.filter((user) => user.id !== id);

		setEmployeeList(updatedEmployeeList);
		localStorage.setItem(
			"employeeList",
			JSON.stringify(updatedEmployeeList)
		);
	}
	return (
		<>
			<li>
				<div className={employeeInfoCardStyles.employeeDiv}>
					<div className={employeeInfoCardStyles.employeePhoto}>
						<img src={user.photo} alt={user.fullName} />
					</div>

					<div className={employeeInfoCardStyles.employeeInfo}>
						<div className={employeeInfoCardStyles.infoItem}>
							<span className={employeeInfoCardStyles.infoName}>
								{user.fullName}
							</span>
						</div>

						<div className={employeeInfoCardStyles.infoItem}>
							<span
								className={employeeInfoCardStyles.infoEmployer}>
								{user.employerName.length > 30
									? user.employerName.substring(0, 30) + "..."
									: user.employerName}
							</span>
						</div>

						<div className={employeeInfoCardStyles.infoItem}>
							<span className={employeeInfoCardStyles.infoTrade}>
								{user.trade}
							</span>
						</div>
					</div>

					<span
						className={employeeInfoCardStyles.deleteBtn}
						onClick={(e) => handleDelete(user.id)}>
						<IconTrashFilled size={17} />
					</span>
				</div>
			</li>
		</>
	);
};

export default EmployeeInfoCard;
