import courseCardStyles from "./CourseCard.module.css";

const CourseCard = ({ course, index, selectedCourse, setSelectedCourse }) => {
	return (
		<>
			<div
				className={`${courseCardStyles.courseDiv} ${
					course?.name === selectedCourse?.name
						? courseCardStyles.selected
						: ""
				}`}
				onClick={(e) => setSelectedCourse(course)}>
				<div className={courseCardStyles.courseChip}>
					{course.courseID}
				</div>
				<div className={courseCardStyles.courseTitle}>
					<span className={courseCardStyles.courseSerialNo}>
						{index + 1 < 10 ? "0" + (index + 1) : index + 1}
						.&nbsp;&nbsp;
					</span>
					{course.name}
				</div>

				<div className={courseCardStyles.courseDescription}>
					{course?.description?.length > 0 // If description exists
						? course.description.length > 243 // If description too long
							? course.description.substring(0, 243) + "..."
							: course.description
						: "~~No Description~~"}
				</div>
			</div>
		</>
	);
};

export default CourseCard;
