import searchBarStyles from "./SearchBar.module.css";
import { searchBar_placeholderText } from "../../../utils/infoStrings";
import { IconSearch, IconX } from "@tabler/icons-react";

const SearchBar = ({ searchTerm, setSearchTerm }) => {
	return (
		<div className={searchBarStyles.searchDiv}>
			<div className={searchBarStyles.searchBar}>
				<span className={searchBarStyles.searchIcon}>
					<IconSearch size={16} />
				</span>
				<input
					type="text"
					placeholder={searchBar_placeholderText}
					value={searchTerm}
					onChange={(e) => setSearchTerm(e.target.value)}
					className={searchBarStyles.searchInput}
				/>
				{searchTerm && (
					<span
						className={searchBarStyles.clearSearch}
						onClick={(e) => setSearchTerm("")}>
						<IconX size={16} />
					</span>
				)}
			</div>
		</div>
	);
};

export default SearchBar;
