import menuButtonStyles from "./MenuBtton.module.css";
import { IconLoader, IconChevronRight } from "@tabler/icons-react";

import Cookies from "js-cookie";

import { useState } from "react";
import { useNavigate } from "react-router-dom";
import {
	awareIMContextPath,
	baseURL,
	signOffEndpt,
} from "../../utils/endpoints";

const MenuButton = ({ btnIcon, btnLink, btnText }) => {
	const navigate = useNavigate();

	const [isLoading, setIsLoading] = useState(false);

	function handleClick() {
		if (!btnLink) {
			handleSignOff();
		} else {
			navigate(btnLink);
		}
	}

	const loggedInUser = Cookies.get("user")
		? JSON.parse(Cookies.get("user"))
		: "";

	const handleSignOff = async () => {
		setIsLoading(true);

		const signOffBodyParams = {
			api_key: loggedInUser?.token,
			member_id: loggedInUser?.id,
		};
		// console.log(signOffBodyParams);

		const res = await fetch(
			`${baseURL}${awareIMContextPath}${signOffEndpt}`,
			{
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify(signOffBodyParams),
			}
		);
		const resData = await res.json();
		// console.log(resData);

		Cookies.remove("user");
		localStorage.removeItem("allTrainingEvents");

		// For flash message
		sessionStorage.setItem(
			"messageToFlash",
			JSON.stringify({
				status: resData.status,
				message: resData.message,
				showFlashMessage: true,
			})
		);

		setIsLoading(false);
		window.location.reload(true);
	};

	return (
		<>
			<button className={menuButtonStyles.menuItem} onClick={handleClick}>
				{isLoading ? (
					<IconLoader className="loaderSvg" />
				) : (
					<>
						<div className={menuButtonStyles.svgDiv}>{btnIcon}</div>
						{btnText}
						<IconChevronRight
							className={menuButtonStyles.menuItemChevron}
						/>
					</>
				)}
			</button>
		</>
	);
};

export default MenuButton;
