import historyStyles from "./History.module.css";
import {
	baseURL,
	awareIMContextPath,
	newTrainingEventEndpt,
} from "../../utils/endpoints";
import {
	history_noContentHeadingText,
	history_noContentSubheadingText,
} from "../../utils/infoStrings";

import Cookies from "js-cookie";

import { useUpdateHeader } from "../../utils/functionList";

import EventCard from "./EventCard";
import { useEffect, useState } from "react";

const History = () => {
	useUpdateHeader(
		{ isBackButton: true, backLink: "/", confirmOnExit: false },
		"History",
		false,
		"History"
	);

	const [allTrainingEvents, setAllTrainingEvents] = useState(
		localStorage.getItem("allTrainingEvents")
			? JSON.parse(localStorage.getItem("allTrainingEvents"))
			: []
	);

	const loggedInUser = Cookies.get("user")
		? JSON.parse(Cookies.get("user"))
		: "";

	// Sort events in descending order on the basis of event time
	allTrainingEvents.sort((a, b) =>
		a.scheduled_on > b.scheduled_on ? -1 : 1
	);

	const pendingEvents = allTrainingEvents.filter(
		(event) => event.status === 0
	);

	useEffect(() => {
		const updateTrainingEvent = async () => {
			const eventToUpdate = pendingEvents[0];

			// Send a POST request and update the status value
			try {
				const res = await fetch(
					`${baseURL}${awareIMContextPath}${newTrainingEventEndpt}`,
					{
						method: "POST",
						headers: {
							"Content-Type": "application/json",
						},
						body: JSON.stringify({
							api_key: loggedInUser.token,
							member_id: loggedInUser.id,
							training_event: {
								scheduled_on: eventToUpdate.event.scheduled_on,
								course_id: eventToUpdate.event.course.id,
								participants: eventToUpdate.event.trainees.map(
									(trainee) => ({
										participant_id: trainee.id,
									})
								),
							},
						}),
					}
				);
				const resData = await res.json();

				if (res.status === 200) {
					// Create a new copy of the eventToUpdate and update its status
					const updatedEvent = {
						...eventToUpdate,
						status: resData.status,
					};

					// Create a new array that replaces eventToUpdate with updatedEvent
					const updatedAllEvents = allTrainingEvents.map((event) =>
						event.scheduled_on === eventToUpdate.scheduled_on
							? updatedEvent
							: event
					);

					setAllTrainingEvents(updatedAllEvents);
					localStorage.setItem(
						"allTrainingEvents",
						JSON.stringify(updatedAllEvents)
					);
				} else {
					console.error("Request failed with status:", res.status);
				}
			} catch (error) {
				console.error("Request failed with error:", error);
			}

			// remove the updated event from pending events
			pendingEvents.shift();
		};

		// Start updating the training events
		if (pendingEvents.length) {
			updateTrainingEvent();
		}
	}, [pendingEvents]);

	return (
		<>
			<div
				className={`${historyStyles.container} ${
					!allTrainingEvents.length
						? historyStyles.noContentContainer
						: ""
				}`}>
				{!allTrainingEvents.length ? (
					<div className={historyStyles.noContent}>
						<div className={historyStyles.noContentImg}>
							<img
								src="/images/no_content.png"
								alt="No Content"
							/>
						</div>

						<div className={historyStyles.noContentInfo}>
							<span className={historyStyles.noContentTitle}>
								{history_noContentHeadingText}
							</span>
							<span className={historyStyles.noContentText}>
								{history_noContentSubheadingText}
							</span>
						</div>
					</div>
				) : (
					<ul className={historyStyles.trainingEventsList}>
						{allTrainingEvents.map((event, index) => {
							return (
								<li
									key={index}
									className={
										historyStyles.trainingEventsListItem
									}>
									<EventCard event={event} />
								</li>
							);
						})}
					</ul>
				)}
			</div>
		</>
	);
};

export default History;
