import { useContext, useEffect, useMemo } from "react";
import HeaderContext from "../context/HeaderContext";

// custom hook to update header in each component
export const useUpdateHeader = (
	backButton,
	heading,
	isSearchButton,
	currentComponent
) => {
	const { setHeaderContent } = useContext(HeaderContext);
	const memoizedBackButton = useMemo(
		() => backButton,
		[backButton.isBackButton, backButton.backLink, backButton.confirmOnExit]
	);

	useEffect(() => {
		setHeaderContent({
			backButton: memoizedBackButton,
			heading,
			isSearchButton,
			currentComponent,
		});
	}, [
		setHeaderContent,
		memoizedBackButton,
		heading,
		isSearchButton,
		currentComponent,
	]);
};
