import notFoundStyles from "./NotFoundPage.module.css";

import { useUpdateHeader } from "../../utils/functionList";
import { Link } from "react-router-dom";

const NotFoundPage = () => {
	useUpdateHeader(
		{ isBackButton: false, backLink: "", confirmOnExit: false },
		"",
		false,
		"NotFoundPage"
	);

	return (
		<div className={notFoundStyles.container}>
			<div className={notFoundStyles.content}>
				<h1 className={notFoundStyles.title}>404</h1>
				<p className={notFoundStyles.text}>
					Sneaking around? This page does not exist.
				</p>
			</div>
			<Link to="/" className={notFoundStyles.link}>
				Go Home
			</Link>
		</div>
	);
};

export default NotFoundPage;
