import layoutStyles from "./Layout.module.css";
import { IconArrowLeft } from "@tabler/icons-react";
import { IconSearch } from "@tabler/icons-react";
import {
	layout_footerCopyrightText,
	layout_modalHeadingText,
	layout_modalSubheadingText,
} from "../../../utils/infoStrings";

import Modal from "react-modal";

import HeaderContext from "../../../context/HeaderContext";
import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";

// Modal styles have to be an object defined in this component. Do not move this to a stylesheet
const modalStyles = {
	overlay: {
		backgroundColor: "rgba(12, 9, 30, 0.7)",
		backdropFilter: "blur(1px)",
		zIndex: 9999,
	},
	content: {
		position: "absolute",
		top: "50%",
		left: "50%",
		transform: "translate(-50%, -50%)",
		width: "281px",
		height: "175px",
		maxWidth: "90%",
		maxHeight: "90%",
		overflow: "auto",
		background: "#ffffff",
		borderRadius: "4px",
		padding: "24px",
		boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.2)",
	},
};

const Layout = ({ children }) => {
	const { headerContent } = useContext(HeaderContext);
	// 	console.log(headerContent);

	const navigate = useNavigate();

	const [showModal, setShowModal] = useState(false);

	function onClickBackBtn() {
		if (headerContent.backButton.confirmOnExit === true) {
			setShowModal(true);
		} else {
			navigate(headerContent.backButton.backLink);
		}
	}

	function onClickModalNoBtn() {
		setShowModal(false);
	}

	function onClickModalYesBtn() {
		localStorage.removeItem("employeeList");
		navigate(headerContent.backButton.backLink);
		setShowModal(false);
	}

	const componentsToDisplayFooter = ["Login", "Menu", "NotFoundPage"];

	return (
		<div className={layoutStyles.container}>
			<div
				className={
					headerContent.currentComponent === "Login"
						? layoutStyles.login
						: layoutStyles.header
				}>
				<div className={layoutStyles.backBtnContainer}>
					{headerContent.backButton.isBackButton && (
						<button onClick={onClickBackBtn}>
							<div className={layoutStyles.backBtn}>
								<IconArrowLeft
									size={24}
									stroke={2}
									color="#ffffff"
								/>
							</div>
						</button>
					)}
				</div>
				<div className={layoutStyles.heading}>
					{headerContent.heading !== "" ? (
						headerContent.heading
					) : (
						<div className={layoutStyles.logo}>
							<img
								src={
									headerContent.currentComponent === "Login"
										? "/images/workxid_logo_dark.png"
										: "/images/workxid_logo_white.png"
								}
								alt="WorkXid logo"
							/>
						</div>
					)}
				</div>

				<div className={layoutStyles.otherBtnContainer}>
					{headerContent.currentComponent === "Scanner" && (
						<div
							onClick={() => navigate("/alldata")}
							className={layoutStyles.otherBtn}>
							<svg
								width="20"
								height="20"
								viewBox="0 0 15 16"
								fill="none"
								xmlns="http://www.w3.org/2000/svg">
								<path
									fillRule="evenodd"
									clipRule="evenodd"
									d="M9.47491 5.49838C9.47491 7.37104 8.03263 8.87175 6.23289 8.87175C4.4338 8.87175 2.99087 7.37104 2.99087 5.49838C2.99087 3.62571 4.4338 2.125 6.23289 2.125C8.03263 2.125 9.47491 3.62571 9.47491 5.49838ZM1.33333 12.6915C1.33333 10.9579 3.59035 10.5242 6.23289 10.5242C8.88978 10.5242 11.1324 10.9729 11.1324 12.7078C11.1324 14.4413 8.87542 14.875 6.23289 14.875C3.576 14.875 1.33333 14.4263 1.33333 12.6915ZM10.7823 5.55953C10.7823 6.51317 10.507 7.40301 10.0243 8.14216C9.97406 8.21818 10.0184 8.32068 10.1058 8.33697C10.2272 8.35801 10.3517 8.3709 10.479 8.37362C11.7444 8.40823 12.8801 7.56048 13.1939 6.28375C13.659 4.38937 12.2944 2.68843 10.5559 2.68843C10.3674 2.68843 10.1867 2.70879 10.0106 2.74612C9.98645 2.75155 9.96036 2.76377 9.94731 2.78549C9.93035 2.81332 9.94275 2.84929 9.95971 2.87305C10.4822 3.63528 10.7823 4.56313 10.7823 5.55953ZM12.8782 9.70579C13.7288 9.87888 14.2878 10.2311 14.5194 10.745C14.7158 11.1658 14.7158 11.6545 14.5194 12.0753C14.1652 12.8708 13.0223 13.1267 12.5781 13.1925C12.4861 13.2061 12.4124 13.1246 12.4222 13.0283C12.6492 10.8237 10.8442 9.77842 10.3772 9.53814C10.3576 9.52661 10.3531 9.51032 10.355 9.49946C10.3563 9.49267 10.3648 9.48181 10.3798 9.47977C11.3902 9.45941 12.477 9.60398 12.8782 9.70579Z"
									fill="#ffffff"
								/>
							</svg>
						</div>
					)}
					{headerContent.isSearchButton && (
						<div className={layoutStyles.otherBtn}>
							<IconSearch color="#ffffff" />
						</div>
					)}
				</div>
			</div>

			<div className={layoutStyles.content}>
				<div className={layoutStyles.children}>{children}</div>

				{componentsToDisplayFooter.includes(
					headerContent.currentComponent
				) && (
					<div className={layoutStyles.footer}>
						<span className={layoutStyles.copyright}>
							{layout_footerCopyrightText}
						</span>
						<div className={layoutStyles.links}>
							<a
								href="https://workxid.com/privacyandterms.php#PrivacyPolicy"
								className={layoutStyles.leftLink}
								target="_blank"
								rel="noreferrer">
								Privacy Policy
							</a>
							<a
								href="https://workxid.com/privacyandterms.php"
								className={layoutStyles.rightLink}
								target="_blank"
								rel="noreferrer">
								Terms of Use
							</a>
						</div>
					</div>
				)}
			</div>

			{/* Modal */}
			<Modal
				isOpen={showModal}
				onRequestClose={() => setShowModal(false)}
				style={modalStyles}
				contentLabel="Response Modal"
				parentSelector={() =>
					document.querySelector(`.${layoutStyles.container}`)
				}>
				<div className={layoutStyles.modalContainer}>
					<div className={layoutStyles.modalTitle}>
						{layout_modalHeadingText}
					</div>
					<div className={layoutStyles.modalText}>
						{layout_modalSubheadingText}
					</div>
					<div className={layoutStyles.modalBtns}>
						<button
							className={layoutStyles.modalNoBtn}
							onClick={onClickModalNoBtn}>
							No, Thanks
						</button>
						<button
							className={layoutStyles.modalYesBtn}
							onClick={onClickModalYesBtn}>
							Yes
						</button>
					</div>
				</div>
			</Modal>
		</div>
	);
};

export default Layout;
