import flashMessageStyles from "./FlashMessage.module.css";
import { useState, useEffect } from "react";

const FlashMessage = () => {
	const [showFlashMsg, setShowFlashMsg] = useState(false);
	const [message, setMessage] = useState("");
	const [flashMsgTheme, setFlashMsgTheme] = useState();

	const flashOnComponentMount = () => {
		const messageToFlash = sessionStorage.getItem("messageToFlash")
			? JSON.parse(sessionStorage.getItem("messageToFlash"))
			: {};

		setShowFlashMsg(messageToFlash.showFlashMessage);
		setMessage(messageToFlash.message);
		setFlashMsgTheme(messageToFlash.status);
	};

	useEffect(() => {
		flashOnComponentMount();

		const timer = setTimeout(() => {
			setShowFlashMsg(false);
			setMessage("");
			sessionStorage.clear();
		}, 2000);

		return () => clearTimeout(timer);
	}, []);

	return (
		<>
			<div className={flashMessageStyles.msgContainer}>
				{showFlashMsg && (
					<div
						className={`${flashMessageStyles.loginMsg} ${
							flashMessageStyles.fadeOut
						} ${
							flashMsgTheme === 0
								? flashMessageStyles.failed
								: flashMessageStyles.success
						}`}>
						<span className="message">{message}</span>
					</div>
				)}
			</div>
		</>
	);
};

export default FlashMessage;
